.find-movie {
  height: 56px;
  flex-grow: 1;
  display: flex;
  gap: 30px;
}

.controlers-block {
  margin-top: 50px;
}

.popup {
  background-color: rgba(0, 0, 0, 0.8);
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;

  &__content {
    background-color: rgba(255, 255, 255, 0.886);
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 140px;
    padding: 60px 40px;
  }

}


